/**
 * ==========================================
 * /api/elevation — وكيل Open-Meteo Elevation
 * ==========================================
 * مثال طلب:
 *   GET /api/elevation?lat=35.76&lon=-5.83
 *
 * مثال استجابة:
 * {
 *   ok: true,
 *   lat: 35.76,
 *   lon: -5.83,
 *   elevation_m: 45.2,
 *   provider: "open-meteo"
 * }
 */

import { jsonResponse, handleOptions, serverError } from "../utils.js";

const ELEVATION_URL = "https://api.open-meteo.com/v1/elevation";

function getCacheTtl(env) {
  const v = env.CACHE_TTL_LONG || "21600"; // 6 ساعات افتراضيًا
  const n = parseInt(v, 10);
  return Number.isFinite(n) && n > 0 ? n : 21600;
}

/** مفتاح الكاش في KV */
function cacheKeyForElevation(lat, lon) {
  return `elev:${lat.toFixed(4)},${lon.toFixed(4)}`;
}

export async function handleElevation(request, env) {
  // دعم OPTIONS لـ CORS
  const opt = handleOptions(request);
  if (opt) return opt;

  if (request.method !== "GET") {
    return jsonResponse({ ok: false, error: "Method not allowed" }, 405);
  }

  const url = new URL(request.url);
  const latStr = url.searchParams.get("lat");
  const lonStr = url.searchParams.get("lon");

  if (!latStr || !lonStr) {
    return jsonResponse({ ok: false, error: "Missing 'lat' or 'lon' query parameters" }, 400);
  }

  const lat = parseFloat(latStr);
  const lon = parseFloat(lonStr);

  if (!Number.isFinite(lat) || !Number.isFinite(lon)) {
    return jsonResponse({ ok: false, error: "Invalid 'lat' or 'lon' values" }, 400);
  }

  const cacheKey = cacheKeyForElevation(lat, lon);

  try {
    // 1) الكاش من KV
    if (env.PRAYER_KV) {
      const cached = await env.PRAYER_KV.get(cacheKey);
      if (cached) {
        try {
          const json = JSON.parse(cached);
          return jsonResponse({ ...json, cached: true }, 200);
        } catch {
          // تجاهل الكاش الفاسد
        }
      }
    }

    // 2) استدعاء Open-Meteo Elevation
    const params = new URLSearchParams({
      latitude: String(lat),
      longitude: String(lon),
    });

    const res = await fetch(`${ELEVATION_URL}?${params.toString()}`);
    if (!res.ok) {
      return jsonResponse(
        { ok: false, error: "Upstream elevation error", status: res.status },
        502
      );
    }

    const data = await res.json();
    // شكل الاستجابة: { elevation: [value], latitude: [...], longitude: [...] }
    const elevationArray = data.elevation;
    const elevation_m =
      Array.isArray(elevationArray) && elevationArray.length > 0
        ? Number(elevationArray[0])
        : null;

    if (!Number.isFinite(elevation_m)) {
      const payload = {
        ok: false,
        lat,
        lon,
        error: "No elevation data",
        provider: "open-meteo",
      };
      if (env.PRAYER_KV) {
        await env.PRAYER_KV.put(cacheKey, JSON.stringify(payload), {
          expirationTtl: 600, // 10 دقائق
        });
      }
      return jsonResponse(payload, 404);
    }

    const payload = {
      ok: true,
      lat,
      lon,
      elevation_m,
      provider: "open-meteo",
    };

    // 3) حفظ في الكاش
    if (env.PRAYER_KV) {
      await env.PRAYER_KV.put(cacheKey, JSON.stringify(payload), {
        expirationTtl: getCacheTtl(env),
      });
    }

    return jsonResponse(payload, 200);
  } catch (err) {
    return serverError(err);
  }
}
