/**
 * ==========================================
 * /api/geocode — وكيل بسيط لـ Nominatim (OSM)
 * ==========================================
 * مثال طلب:
 *   GET /api/geocode?q=Tanger, Morocco
 *
 * مثال استجابة:
 * {
 *   ok: true,
 *   query: "Tanger, Morocco",
 *   provider: "nominatim",
 *   result: {
 *     display_name: "...",
 *     lat: 35.779,
 *     lon: -5.813,
 *     boundingbox: [...],
 *     type: "city",
 *     class: "place"
 *   }
 * }
 */

import { jsonResponse, handleOptions, serverError } from "../utils.js";

const NOMINATIM_URL = "https://nominatim.openstreetmap.org/search";

/** قراءة TTL من المتغيّرات (بالثواني) */
function getCacheTtl(env) {
  const v = env.CACHE_TTL_LONG || "21600"; // 6 ساعات افتراضيًا
  const n = parseInt(v, 10);
  return Number.isFinite(n) && n > 0 ? n : 21600;
}

/** مفتاح الكاش في KV */
function cacheKeyForQuery(q) {
  return "geo:" + q.trim().toLowerCase();
}

export async function handleGeocode(request, env) {
  // دعم OPTIONS لـ CORS
  const opt = handleOptions(request);
  if (opt) return opt;

  if (request.method !== "GET") {
    return jsonResponse({ ok: false, error: "Method not allowed" }, 405);
  }

  const url = new URL(request.url);
  const q = (url.searchParams.get("q") || "").trim();

  if (!q) {
    return jsonResponse({ ok: false, error: "Missing 'q' query parameter" }, 400);
  }

  const cacheKey = cacheKeyForQuery(q);

  try {
    // 1) جرّب جلب النتيجة من KV (كاش)
    if (env.PRAYER_KV) {
      const cached = await env.PRAYER_KV.get(cacheKey);
      if (cached) {
        try {
          const json = JSON.parse(cached);
          return jsonResponse({ ...json, cached: true }, 200);
        } catch {
          // لو الكاش فاسد نتجاهله ونكمل
        }
      }
    }

    // 2) طلب حقيقي من Nominatim
    const params = new URLSearchParams({
      q,
      format: "json",
      limit: "1",
      addressdetails: "0",
    });

    const res = await fetch(`${NOMINATIM_URL}?${params.toString()}`, {
      headers: {
        // مهم: سياسة Nominatim تتطلب User-Agent واضح
        "User-Agent": "prayer-times-app/0.1 (contact: example@example.com)",
      },
    });

    if (!res.ok) {
      return jsonResponse(
        { ok: false, error: "Upstream geocoding error", status: res.status },
        502
      );
    }

    const data = await res.json();
    if (!Array.isArray(data) || data.length === 0) {
      const payload = {
        ok: false,
        query: q,
        error: "No results",
      };
      // حفظ نتيجة "لا شيء" لفترة قصيرة لتقليل الطلبات المكررة
      if (env.PRAYER_KV) {
        await env.PRAYER_KV.put(cacheKey, JSON.stringify(payload), {
          expirationTtl: 600, // 10 دقائق
        });
      }
      return jsonResponse(payload, 404);
    }

    const item = data[0];
    const result = {
      display_name: item.display_name,
      lat: parseFloat(item.lat),
      lon: parseFloat(item.lon),
      boundingbox: item.boundingbox,
      type: item.type,
      class: item.class,
    };

    const payload = {
      ok: true,
      query: q,
      provider: "nominatim",
      result,
    };

    // 3) حفظ في الكاش (KV)
    if (env.PRAYER_KV) {
      await env.PRAYER_KV.put(cacheKey, JSON.stringify(payload), {
        expirationTtl: getCacheTtl(env),
      });
    }

    return jsonResponse(payload, 200);
  } catch (err) {
    return serverError(err);
  }
}
