/**
 * ==========================================
 * /api/weather — وكيل Open-Meteo Forecast
 * ==========================================
 * الغرض: جلب حرارة/ضغط/… لاستخدامها في تصحيح الانكسار أو العرض
 *
 * مثال طلب:
 *   GET /api/weather?lat=35.76&lon=-5.83&hourly=temperature_2m,pressure_msl
 *
 * مثال استجابة:
 * {
 *   ok: true,
 *   lat: 35.76,
 *   lon: -5.83,
 *   provider: "open-meteo",
 *   data: { ... نفس شكل Open-Meteo تقريبًا ... }
 * }
 */

import { jsonResponse, handleOptions, serverError } from "../utils.js";

const WEATHER_URL = "https://api.open-meteo.com/v1/forecast";

function getCacheTtl(env) {
  const v = env.CACHE_TTL_SHORT || "3600"; // 1 ساعة افتراضيًا
  const n = parseInt(v, 10);
  return Number.isFinite(n) && n > 0 ? n : 3600;
}

/** مفتاح الكاش في KV، نعتمد lat/lon و hourly params */
function cacheKeyForWeather(lat, lon, hourly) {
  const h = (hourly || "").trim().toLowerCase() || "temperature_2m,pressure_msl";
  return `meteo:${lat.toFixed(3)},${lon.toFixed(3)}:${h}`;
}

export async function handleWeather(request, env) {
  // دعم OPTIONS لـ CORS
  const opt = handleOptions(request);
  if (opt) return opt;

  if (request.method !== "GET") {
    return jsonResponse({ ok: false, error: "Method not allowed" }, 405);
  }

  const url = new URL(request.url);
  const latStr = url.searchParams.get("lat");
  const lonStr = url.searchParams.get("lon");
  const hourly = url.searchParams.get("hourly") || "temperature_2m,pressure_msl";

  if (!latStr || !lonStr) {
    return jsonResponse({ ok: false, error: "Missing 'lat' or 'lon' query parameters" }, 400);
  }

  const lat = parseFloat(latStr);
  const lon = parseFloat(lonStr);

  if (!Number.isFinite(lat) || !Number.isFinite(lon)) {
    return jsonResponse({ ok: false, error: "Invalid 'lat' or 'lon' values" }, 400);
  }

  const cacheKey = cacheKeyForWeather(lat, lon, hourly);

  try {
    // 1) الكاش من KV
    if (env.PRAYER_KV) {
      const cached = await env.PRAYER_KV.get(cacheKey);
      if (cached) {
        try {
          const json = JSON.parse(cached);
          return jsonResponse({ ...json, cached: true }, 200);
        } catch {
          // تجاهل إن الكاش كان فاسد
        }
      }
    }

    // 2) استدعاء Open-Meteo
    const params = new URLSearchParams({
      latitude: String(lat),
      longitude: String(lon),
      hourly,
      timezone: "auto",
    });

    const res = await fetch(`${WEATHER_URL}?${params.toString()}`);
    if (!res.ok) {
      return jsonResponse(
        { ok: false, error: "Upstream weather error", status: res.status },
        502
      );
    }

    const data = await res.json();

    const payload = {
      ok: true,
      lat,
      lon,
      provider: "open-meteo",
      data,
    };

    // 3) حفظ في الكاش
    if (env.PRAYER_KV) {
      await env.PRAYER_KV.put(cacheKey, JSON.stringify(payload), {
        expirationTtl: getCacheTtl(env),
      });
    }

    return jsonResponse(payload, 200);
  } catch (err) {
    return serverError(err);
  }
}
