/**
 * ==========================================
 * /api/usage — عرض حالة الخطة والاستهلاك
 * ==========================================
 * يعيد:
 * {
 *   ok: true,
 *   plan: "pro"|"free"|"org",
 *   monthly_quota: 50000,
 *   used: 123,
 *   remaining: 49877,
 *   period: "YYYY-MM",
 *   expires_at: "ISO",
 *   days_left: 12
 * }
 */

import { jsonResponse, handleOptions, daysBetween } from "../utils.js";
import { requireApiAuth, getMonthlyUsage, ymNow } from "../auth.js";

export async function handleUsage(request, env) {
  // دعم طلبات CORS preflight
  const opt = handleOptions(request);
  if (opt) return opt;

  if (request.method !== "GET") {
    return jsonResponse({ ok: false, error: "Method not allowed" }, 405);
  }

  // تحقق من مفتاح API
  const auth = await requireApiAuth(request, env);
  if (!auth.authorized) return auth.response;

  const { apiKey, record } = auth;
  const period = ymNow();
  const used = await getMonthlyUsage(env, apiKey, period);
  const monthly_quota = Number(record.quota_monthly ?? env.DEFAULT_PRO_QUOTA ?? 50000);
  const remaining = Math.max(0, monthly_quota - used);

  // حساب الأيام المتبقية حتى انتهاء المفتاح
  let days_left = null;
  if (record.expires_at) {
    const now = new Date();
    const exp = new Date(record.expires_at);
    days_left = Math.max(0, daysBetween(exp, now)); // عدد الأيام المطلقة حتى الانتهاء
  }

  // رؤوس معلوماتية اختيارية
  const headers = {
    "X-Plan": record.plan || "free",
    "X-Used": String(used),
    "X-Remaining": String(remaining),
    ...(record.expires_at ? { "X-Expiry": record.expires_at, "X-Expiry-Days": String(days_left) } : {}),
  };

  return jsonResponse(
    {
      ok: true,
      plan: record.plan || "free",
      monthly_quota,
      used,
      remaining,
      period,
      expires_at: record.expires_at || null,
      days_left,
    },
    200,
    headers
  );
}
