/**
 * ==========================================
 * /admin/* — لوحة المسؤول (مفاتيح شهرية)
 * ==========================================
 * المصادقة: Basic Auth (كلمة المرور في SECRET: ADMIN_PASSWORD)
 *
 * المسارات:
 *   POST /admin/keys
 *     الجسم: { plan?, quota_monthly?, expires_at?, expires_in_days?, is_active?, notes? }
 *     يرجع: { ok, key, record }
 *
 *   PATCH /admin/keys/:key
 *     الجسم: أي من الحقول أعلاه لتعديلها
 *     يرجع: { ok, key, record }
 *
 *   GET /admin/keys/:key
 *     يرجع: { ok, key, record }
 *
 *   GET /admin/search?prefix=<str>&limit=<n>
 *     يسرد المفاتيح الموجودة (بالـ prefix إن وُجد)
 *     يرجع: { ok, items: [{ key, record }] }
 */

import { jsonResponse, handleOptions, nowIso } from "../utils.js";
import { requireAdminAuth, getKeyRecord, saveKeyRecord } from "../auth.js";

/** توليد مفتاح API عشوائي */
function randomKey(len = 48) {
  const bytes = new Uint8Array(len);
  crypto.getRandomValues(bytes);
  // حروف آمنة للـ URL
  const alphabet = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
  let out = "pt_";
  for (let i = 0; i < len; i++) out += alphabet[bytes[i] % alphabet.length];
  return out;
}

/** إضافة أيام إلى تاريخ ISO (UTC) وإرجاع ISO */
function addDaysIso(iso, days) {
  const d = iso ? new Date(iso) : new Date();
  d.setUTCDate(d.getUTCDate() + Number(days));
  return d.toISOString().split(".")[0] + "Z";
}

/** استخراج جزء المسار بعد /admin */
function subpath(url) {
  const u = new URL(url);
  return u.pathname.replace(/^\/admin\/?/, "");
}

export async function handleAdmin(request, env) {
  // OPTIONS (CORS preflight)
  const opt = handleOptions(request);
  if (opt) return opt;

  // مصادقة مسؤول
  const admin = requireAdminAuth(request, env);
  if (!admin.authorized) return admin.response;

  const method = request.method;
  const path = subpath(request.url);

  try {
    // -----------------------------------------
    // POST /admin/keys  → إنشاء مفتاح جديد
    // -----------------------------------------
    if (method === "POST" && path === "keys") {
      const body = await request.json().catch(() => ({}));
      const plan = body.plan ?? "pro";
      const quota = Number(body.quota_monthly ?? env.DEFAULT_PRO_QUOTA ?? 50000);
      const is_active = body.is_active === false ? false : true;
      const notes = body.notes ?? "manual via admin";
      const created_at = nowIso();

      // حساب تاريخ الانتهاء
      let expires_at = body.expires_at ?? null;
      const expires_in_days = body.expires_in_days ?? 30;
      if (!expires_at) {
        expires_at = addDaysIso(created_at, expires_in_days);
      }

      // توليد المفتاح وضمان عدم الاصطدام (احتمال ضعيف جدًا)
      let key = randomKey(40);
      let exists = await env.PRAYER_KV.get(`key:${key}`);
      if (exists) key = randomKey(44);

      const record = {
        plan,
        quota_monthly: quota,
        created_at,
        expires_at,
        is_active,
        notes,
      };

      await saveKeyRecord(env, key, record);

      return jsonResponse({ ok: true, key, record }, 201);
    }

    // -----------------------------------------
    // PATCH /admin/keys/:key  → تعديل مفتاح
    // -----------------------------------------
    if (method === "PATCH" && path.startsWith("keys/")) {
      const apiKey = decodeURIComponent(path.split("/")[1] || "");
      if (!apiKey) return jsonResponse({ ok: false, error: "Missing key" }, 400);

      const current = await getKeyRecord(env, apiKey);
      if (!current) return jsonResponse({ ok: false, error: "Key not found" }, 404);

      const body = await request.json().catch(() => ({}));

      // تحديث الحقول المسموح بها
      if (body.plan !== undefined) current.plan = body.plan;
      if (body.quota_monthly !== undefined) current.quota_monthly = Number(body.quota_monthly);
      if (body.is_active !== undefined) current.is_active = !!body.is_active;
      if (body.notes !== undefined) current.notes = String(body.notes);

      if (body.expires_at) {
        current.expires_at = body.expires_at;
      } else if (body.expires_in_days !== undefined) {
        current.expires_at = addDaysIso(nowIso(), Number(body.expires_in_days));
      }

      await saveKeyRecord(env, apiKey, current);
      return jsonResponse({ ok: true, key: apiKey, record: current }, 200);
    }

    // -----------------------------------------
    // GET /admin/keys/:key  → عرض سجل مفتاح
    // -----------------------------------------
    if (method === "GET" && path.startsWith("keys/")) {
      const apiKey = decodeURIComponent(path.split("/")[1] || "");
      if (!apiKey) return jsonResponse({ ok: false, error: "Missing key" }, 400);

      const record = await getKeyRecord(env, apiKey);
      if (!record) return jsonResponse({ ok: false, error: "Key not found" }, 404);

      return jsonResponse({ ok: true, key: apiKey, record }, 200);
    }

    // -----------------------------------------
    // GET /admin/search?prefix=&limit=
    // يسرد مفاتيح مع prefix اختياري (افتراضيًا يسرد الكل حتى limit)
    // -----------------------------------------
    if (method === "GET" && path === "search") {
      const url = new URL(request.url);
      const prefix = url.searchParams.get("prefix") || ""; // جزء من المفتاح بعد "key:"
      const limit = Math.min(parseInt(url.searchParams.get("limit") || "100", 10), 1000);

      // KV.list يدعم prefix على مفاتيح KV نفسها
      const listPrefix = "key:" + prefix;
      const listed = await env.PRAYER_KV.list({ prefix: listPrefix, limit });
      const items = [];

      for (const k of listed.keys) {
        const apiKey = k.name.replace(/^key:/, "");
        const recRaw = await env.PRAYER_KV.get(k.name);
        if (!recRaw) continue;
        let record;
        try {
          record = JSON.parse(recRaw);
        } catch {
          continue;
        }
        items.push({ key: apiKey, record });
      }

      return jsonResponse({ ok: true, items }, 200);
    }

    // مسار غير معروف ضمن /admin
    return jsonResponse({ ok: false, error: "Unknown admin route" }, 404);
  } catch (err) {
    console.error("Admin route error:", err);
    return jsonResponse({ ok: false, error: "Admin handler error" }, 500);
  }
}
