/**
 * ===============================
 *  Cloudflare Worker Entry Point
 *  ملف التشغيل الرئيسي
 * ===============================
 */

import { handleGeocode } from "./routes/geocode.js";
import { handleElevation } from "./routes/elevation.js";
import { handleWeather } from "./routes/weather.js";
import { handleUsage } from "./routes/usage.js";
import { handleAdmin } from "./routes/admin.js";
import { jsonResponse, notFound, corsHeaders } from "./utils.js";

/**
 * Router بسيط يعتمد على مسار الطلب
 */
export default {
  async fetch(request, env, ctx) {
    const url = new URL(request.url);
    const { pathname } = url;

    // معالجة /health لاختبار النشر
    if (pathname === "/health") {
      return new Response(JSON.stringify({ ok: true, status: "healthy" }), {
        headers: { ...corsHeaders(), "content-type": "application/json" },
      });
    }

    // وكيل geocode (بدون مفتاح)
    if (pathname.startsWith("/api/geocode")) {
      return handleGeocode(request, env);
    }

    // وكيل elevation
    if (pathname.startsWith("/api/elevation")) {
      return handleElevation(request, env);
    }

    // وكيل weather
    if (pathname.startsWith("/api/weather")) {
      return handleWeather(request, env);
    }

    // بيانات الخطة والاستهلاك (محمية بمفتاح)
    if (pathname.startsWith("/api/usage")) {
      return handleUsage(request, env);
    }

    // لوحة المسؤول (إنشاء المفاتيح / التفعيل اليدوي)
    if (pathname.startsWith("/admin")) {
      return handleAdmin(request, env);
    }

    // أي مسار غير معروف → 404
    return notFound();
  },
};

/**
 * معالجات الأخطاء العامة
 */
addEventListener("unhandledrejection", (event) => {
  console.error("Unhandled rejection:", event.reason);
});
addEventListener("error", (event) => {
  console.error("Error:", event.message);
});
